#haruna {
  width: 100vw;
  height: 100vh;
  background: #f6f6f6;
  position: relative;

  .container {
    width: 90%;
    max-width: 90%;
    height: 100%;
  }
  .row {
      height: 100%;
  }
  .col-6 {
      height: 100%;
  }
  .picture {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      position: relative;
      z-index: 1;
  }
  .picture img {
      width: 90%;
      height: 80%;
      object-fit: contain;
  }
  .box {
      position: absolute;
      width: 58%;
      height: 60%;
      border: solid 5px #da1e75;
      top: 75px;
      left: 450px;
  }
  .name h2 {
      font-size: 6.5rem;
      font-family: 'Fredoka One', cursive;
      color: #463d42;
  }
  .message {
      position: relative;
      z-index: 2;
  }
  .message p{
      font-size: 1.4rem;
      font-family: 'Didact Gothic', sans-serif;
      line-height: 2.5rem;
      color: #463d42;
  }
}

#tak{
  width: 100vw;
  height: 100vh;

  .container {
      width: 90%;
      max-width: 90%;
      height: 100%;
  }
  .name h2 {
      font-size: 6.5rem;
      font-family: 'Fredoka One', cursive;
      color: #463d42;
  }
  .message {
      position: relative;
      z-index: 2;
  }
  .message p{
      font-size: 1.4rem;
      font-family: 'Didact Gothic', sans-serif;
      line-height: 2.5rem;
      color: #463d42;
  }

  .name h2 {
      margin: 1.8rem 0;
      border-bottom: 5px solid #fff001;
  }
  .row {
      height: 50%;
  }
  .row .col-9 {
      height: 100%;
  }
  .info {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: 90%;
  }

  .picture {
      width: 80%;
      height: 100%;
      float: right;
  }

  .picture img {
      height: 100%;
      width: 100%;
      object-fit: contain;
  }
}
